.header-row {
    position: relative; }

.header-top {
    padding: 25px 0 18px;
    border-bottom: 1px solid $gray; }

.header-bottom {
    padding: 17px 0 24px;
    border-bottom: 1px solid $gray;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.36px;
    &__text {
        margin-bottom: 0;
        font-family: $fontSecond;
        font-style: italic;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        letter-spacing: 0.02em; } }

.h-links {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: flex-end;
    &__item {
        width: 22px;
        height: 22px;
        margin-left: 32px;
        &:hover .h-links__item-icon {
            fill: $activeColor; } }

    &__item-icon {
        width: 100%;
        height: 100%;
        fill: $black;
        @include transitionDefault; } }
