.check {
    position: relative;
    &__input {
        position: absolute;
        z-index: -1;
        visibility: hidden;
        &:checked + .check__label::after {
            display: block; } }
    &__label {
        display: block;
        margin: 8px 0;
        padding-left: 28px;
        position: relative;
        cursor: pointer;
        font-size: 12px;
        color: $black;
        line-height: 16px;
        letter-spacing: 0.02em;
        &::before {
            content: '';
            position: absolute;
            top: -2px;
            left: 0;
            width: 18px;
            height: 18px;
            border: 1px solid #939393; }
        &::after {
            content: '';
            display: none;
            position: absolute;
            top: 2px;
            left: 4px;
            width: 10px;
            height: 10px;
            background-color: $activeColor; } } }


.form {
    &__input {
        margin-bottom: 34px;
        width: 100%;
        height: 36px;
        border: none;
        border-bottom: 1px solid #939393;
        font-weight: 300;
        font-size: 16px;
        line-height: 28px;
        letter-spacing: 0.02em;
        &:placeholder {
            color: #3D3D3D; }
        &:focus {
            border-color: $activeColor; } }
    &__textarea {
        margin-bottom: 22px;
        width: 100%;
        height: 110px;
        padding: 8px 11px;
        border: 1px solid #939393;
        font-weight: 300;
        font-size: 16px;
        line-height: 28px;
        letter-spacing: 0.02em;
        &:placeholder {
            color: #3D3D3D; }
        &:focus {
            border-color: $activeColor; } }
    &__check-wrap {
        display: flex;
        margin-top: 18px;
        align-items: center;
        justify-content: center; } }
