html {
    box-sizing: border-box;
    min-height: 100%; }

*, *:before, *:after {
    margin: 0;
    padding: 0;
    box-sizing: inherit; }

body {
    min-height: 100vh;
    font-family: $fontMain;
    // font-size: 14px
    // font-weight: 400
    // line-height: 24px
    // letter-spacing: 0.36px
    font-weight: 300;
    font-size: 20px;
    line-height: 150%;
    /* or 30px */

    letter-spacing: 0.01em;

    color: $black;
    background-color: #fff;
    position: relative; }

.link {
    position: relative;
    color: inherit;
    text-decoration: none;
    padding-bottom: 4px;
    @include transitionDefault;
    &::after {
        content: '';
        width: 0%;
        height: 1px;
        background-color: $activeColor;
        position: absolute;
        bottom: 0;
        left: 0;
        transition: 0.3s ease; }
    &:hover::after {
        width: 100%; } }

a[href^="tel:"] {
    white-space: nowrap; }

input, button, textarea {
    font-family: $fontMain;
    // border: none
    outline: none;
    -webkit-appearance: none; }

button {
    background: none; }

textarea {
    resize: none; }

input[type="submit"] {
    cursor: pointer; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }




ul {
    margin: 0;
    padding: 0;
    list-style: none; }

img {
    max-width: 100%; }

h1 {
    margin-bottom: 5px;
    font-size: 23px;
    font-weight: 700;
    line-height: 26px;
    text-align: center;
    letter-spacing: 0.57px; }

h2 {
    position: relative;
    padding-bottom: 32px;
    margin-bottom: 28px;
    font-family: 'Cormorant';
    font-weight: 700;
    font-size: 68px;
    line-height: 70px;
    text-transform: uppercase;
    &::after {
        content: '';
        width: 153px;
        height: 1px;
        background: #939393;
        position: absolute;
        bottom: 0;
        left: 0; }
    &.text-center {
        &::after {
            left: 50%;
            transform: translateX(-50%); } } }


h3 {
    margin-bottom: 50px;
    font-family: 'Cormorant';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 120%;
    text-transform: uppercase; }


.subtitle {
    margin-bottom: 50px;
    font-family: 'Cormorant';
    font-style: normal;
    font-weight: 600;
    font-size: 48px;
    line-height: 54px; }

.subtitle-m {
    margin-bottom: 25px;
    font-size: 20px;
    font-weight: 400;
    text-align: center;
    letter-spacing: 0.2px; }

p {
    margin-bottom: 20px; }


.text {
    &-left {
        text-align: left; }
    &-center {
        text-align: center; }
    &-right {
        text-align: right; } }

.l-arrow {
    text-transform: uppercase;
    text-decoration: none;
    color: $black;
    font-size: 14px;
    letter-spacing: 0.36px;
    @include transitionDefault;
    &:hover {
        color: $activeColor;
        .l-arrow__icon {
            fill: $activeColor; } }
    &::after {
        display: none; }
    &__icon {
        margin-left: 13px;
        width: 30px;
        height: 10px;
        transition: 0.3s ease;
        fill: $black; } }


.logo-wrap {
    @include center; }
.logo {
    @include center;
    &--header {
        margin-top: -5px; } }

.s-block {
    padding: 135px 0 30px; }

.s-block-gray {
        background-color: $lightGray; }

.masonry {
    & > *:nth-child(even) {
        margin-top: -240px;
 } }        // transform: translateY(-240px)

.more-block {
    margin-top: 132px;
    font-weight: 300;
    font-size: 20px;
    letter-spacing: 0.01em;
    text-align: center;
    &__btn {
        margin-top: 4px; } }

.img-block {
    display: block; }

.notice {
    margin: 5px 0;
    font-size: 12px;
    line-height: 28px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #5B5B5B; }
